import React, { useEffect, useRef } from 'react';

import kakaoIcon from '../assets/icons/kakaonavi.png';
import naverIcon from '../assets/icons/navermap.png';
import tmapIcon from '../assets/icons/tmap.png';

function Location() {
  const mapElement = useRef(null);
  const { naver } = window;

  useEffect(() => {
    if (!mapElement.current || !naver) return;

    //        .
    const location = new naver.maps.LatLng(37.5112, 127.0315);
    const mapOptions = {
      center: location,
      zoom: 17,
      zoomControl: true,
      zoomControlOptions: {
        position: naver.maps.Position.TOP_RIGHT,
      },
    };
    const map = new naver.maps.Map(mapElement.current, mapOptions);

    new naver.maps.Marker({
      position: location,
      map,
    });
  }, []);

  return (
    <div className="map">
      <div className="title">Location</div>
      <div className="location-wrapper">
        <div className="location">   (2F)</div>
      </div>
      <div ref={mapElement} style={{ minHeight: '300px' }} />
      <div className="app-list">
        <div className="app">
          <img src={naverIcon} alt="" />
           
        </div>
        <div className="app">
          <img src={kakaoIcon} alt="" />
           
        </div>
        <div className="app">
          <img src={tmapIcon} alt="" />
          
        </div>
      </div>
      <div className="location-info">
        <div className="info-item">
          <div className="label"></div>
          <div>
            <span className="contact">Address</span>   645
          </div>
          <div>
            <span className="contact">Contact</span>
            02-3443-5670
          </div>
        </div>
        <div className="info-item">
          <div className="label"></div>
          <div>
            <span className="seven">7 </span> 4   4
          </div>
          <div>
            <span className="nine">9 </span> 2   6
          </div>
        </div>
        <div className="info-item">
          <div className="label"></div>
          <div>
             (23-119)
            <span>
              <span className="blue">147</span>
              <span className="blue">241</span>
              <span className="blue">463</span>
              <span className="green">3412</span>
              <span className="green">4211</span>
            </span>
          </div>
          <div>
             (23-146)
            <span>
              <span className="blue">147</span>
              <span className="blue">463</span>
              <span className="green">4211</span>
            </span>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Location;
